import React, { useState } from 'react';
import './OptimizedImage.css';

const OptimizedImage = ({ src, alt, className, loading = 'lazy', ...props }) => {
  const [imgSrc, setImgSrc] = useState(() => {
    if (src && /\.(jpg|jpeg|png)$/i.test(src)) {
      return src.replace(/\.(jpg|jpeg|png)$/i, '.webp');
    }
    return src;
  });
  const [isLoading, setIsLoading] = useState(true);
  const [hasError, setHasError] = useState(false);

  const handleError = () => {
    if (imgSrc.includes('.webp') && !hasError) {
      setImgSrc(src); // Fallback to original format
      setHasError(true);
    }
  };

  const handleLoad = () => {
    setIsLoading(false);
  };

  return (
    <>
      {isLoading && <div className={`image-placeholder ${className || ''}`} />}
      <img
        src={imgSrc}
        alt={alt}
        className={`${className || ''} ${isLoading ? 'loading' : 'loaded'}`}
        loading={loading}
        onError={handleError}
        onLoad={handleLoad}
        {...props}
      />
    </>
  );
};

export default OptimizedImage;