import React, { useState } from 'react';
import { useProducts } from '../../context/ProductContext';
import ProductCard from '../../elements/ProductCard';
import OptimizedImage from '../OptimizedImage';
import './MobileHomePage.css';

const MobileHomePage = () => {
  const { state } = useProducts();
  const { latestCollection, loading } = state;
  const [showAll, setShowAll] = useState(false);
  
  const initialCount = 4;
  const displayedItems = showAll ? latestCollection : latestCollection.slice(0, initialCount);

  if (loading) {
    return <div className="mobile-loading">Loading...</div>;
  }

  return (
    <div className="mobile-home">
      <div className="mobile-hero">
        <OptimizedImage src="/images/hero_image.png" alt="Shringaarika Collection" className="mobile-hero-image" loading="eager" />
        <div className="mobile-hero-overlay">
          <h1>Shringaarika</h1>
          <p>Premium Indian Ethnic Wear</p>
        </div>
      </div>
      
      <div className="mobile-products">
        <h2>Our Collection</h2>
        <div className="mobile-grid">
          {displayedItems.map((item, index) => (
            <ProductCard 
              key={`${item.category}-${item.id}-${index}`} 
              item={item} 
              category={item.category}
            />
          ))}
        </div>
        {!showAll && latestCollection.length > initialCount && (
          <button 
            className="load-more-btn"
            onClick={() => setShowAll(true)}
          >
            Load More ({latestCollection.length - initialCount} items)
          </button>
        )}
      </div>
    </div>
  );
};

export default MobileHomePage;